.home {
    display: flex;
  
    .homeContainer {
      flex: 6;
  
      .headingCenter {
        font-size: 20px;
        font-weight: 600;
        color: rgb(68, 68, 68);
        text-align: center;
        margin: 3% 0 0;
        height: 30px;
      }
  
      .widgets,
      .charts {
        display: flex;
        padding: 20px;
        gap: 20px;
      }
  
      .charts {
        padding: 5px 20px;
      }
  
      .listContainer {
        -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47);
        box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
        padding: 20px;
        margin: 20px;
  
        .listTitle {
          font-weight: 500;
          color: gray;
          margin-bottom: 15px;
        }
      }
    }
  }